.scoreBoard{

  position: relative;
  margin: 30px auto;
  text-align: center;
}

.gameArena{
  position: relative;
  margin:100px auto;
  padding:0px;
  width: 60vw;
  height: 60vw;
  max-width: 500px;
  max-height: 500px;
  border: 1px solid black;
  background-color: rgba(117, 185, 119, 0.486);
}

.snake-dot{
  position: absolute;
  width: 2%;
  height: 2%;
  background: black;
  border: 1px solid yellow;
  z-index: 2;
  border-radius: 5px;
}
.snake-head{
  position: absolute;
  width: 2%;
  height: 2%;
  background-image: url("pacman-black.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 100% 100%;
  z-index: 3;
  padding: 1px;
}

.snake-head.DOWN{
  transform: rotate(90deg);
}
.snake-head.LEFT{
  transform: rotate(180deg) scaleY(-1);;
}
.snake-head.UP{
  transform: rotate(270deg);
}
.food-dot{
  position: absolute;
  width: 3%;
  height: 3%;
  background-image: url("apple.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 100% 100%;

}